<template>
    <div class="register">
        <div class="register__wrapper">
            <div class="register__wrapper__error-container" v-if="errors.length">
                <div class="uk-alert-danger" uk-alert>
                    <!-- {{errors}} -->
                    <a class="uk-alert-close" uk-close></a>
                    <p v-for="(error,index) in errors" v-bind:key="index">- {{error}}</p>
                </div>

            </div>
            <ValidationObserver class="register__wrapper__vo" v-slot="{ handleSubmit }">

                <form class="register__wrapper__vo__form" v-on:submit.prevent="handleSubmit(onSubmit)">

                    <ValidationProvider class="register__wrapper__vo__form__vp" rules="required" v-slot="{ errors }">
                        <label class="register__wrapper__vo__form__vp__label" for="form-stacked-text">Nombre</label>
                        <input class="register__wrapper__vo__form__vp__input uk-input" v-model="name" type="text" name="name">
                        <span class="register__wrapper__vo__form__vp__error uk-text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>

                    <ValidationProvider class="register__wrapper__vo__form__vp" rules="required" v-slot="{ errors }">
                        <label class="register__wrapper__vo__form__vp__label" for="form-stacked-text">Apellido Paterno</label>
                        <input class="register__wrapper__vo__form__vp__input uk-input" v-model="apaterno" type="text" name="apaterno">
                        <span class="register__wrapper__vo__form__vp__error uk-text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>

                    <ValidationProvider class="register__wrapper__vo__form__vp"  v-slot="{ errors }">
                        <label class="register__wrapper__vo__form__vp__label" for="form-stacked-text">Apellido Materno</label>
                        <input class="register__wrapper__vo__form__vp__input uk-input" v-model="amaterno" type="text" name="amaterno">
                        <span class="register__wrapper__vo__form__vp__error uk-text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                    
                    <ValidationProvider class="register__wrapper__vo__form__vp" rules="email" v-slot="{ errors }">
                        <label class="register__wrapper__vo__form__vp__label" for="form-stacked-text">Correo Electronico (opcional)</label>
                        <input class="register__wrapper__vo__form__vp__input uk-input" v-model="email" type="text" name="email">
                        <span class="register__wrapper__vo__form__vp__error uk-text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>

                    <ValidationProvider class="register__wrapper__vo__form__vp" rules="required" v-slot="{ errors }">
                        <label class="register__wrapper__vo__form__vp__label" for="form-stacked-text">Genero</label>
                        <select class="register__wrapper__vo__form__vp__input uk-select" v-model="gender" name="gender">
                            <option v-for="(gender, index) in genders" v-bind:key="index" :value="gender.id">{{gender.gender}}</option>
                        </select>
                        <span class="register__wrapper__vo__form__vp__error uk-text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                    
                    <ValidationProvider class="register__wrapper__vo__form__vp" rules="required" v-slot="{ errors }">
                        <label class="register__wrapper__vo__form__vp__label" for="form-stacked-text">Estado civil</label>
                        <select class="register__wrapper__vo__form__vp__input uk-select" v-model="marital" name="marital">
                            <option v-for="(marital_status, index) in ms" v-bind:key="index" :value="marital_status.id">{{marital_status.status}}</option>
                        </select>   
                        <span class="register__wrapper__vo__form__vp__error uk-text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                    
                    <ValidationProvider class="register__wrapper__vo__form__vp" rules="required" v-slot="{ errors }">
                        <label class="register__wrapper__vo__form__vp__label" for="form-stacked-text">Fecha de nacimiento</label>
                        <input class="register__wrapper__vo__form__vp__input" type="date" v-model="birthday">
                        <span class="register__wrapper__vo__form__vp__error uk-text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                    
                    <ValidationProvider class="register__wrapper__vo__form__vp" name="job" rules="required" v-slot="{ errors }">
                        <label class="register__wrapper__vo__form__vp__label" for="form-stacked-text">Ocupacion</label>
                        <select class="register__wrapper__vo__form__vp__input uk-select" v-model="job">
                            <option v-for="(job, index) in jobs" v-bind:key="index" :value="job.id">{{job.name}}</option>
                            
                        </select>
                        <span class="register__wrapper__vo__form__vp__error uk-text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>

                    <ValidationProvider class="register__wrapper__vo__form__vp" name="escolaridad" rules="required" v-slot="{ errors }">
                        <label class="register__wrapper__vo__form__vp__label" for="form-stacked-text">Escolaridad</label>
                        <select class="register__wrapper__vo__form__vp__input uk-select" v-model="escolaridad">
                            <option v-for="(escolaridad, index) in escolaridades" v-bind:key="index" :value="escolaridad.id">{{escolaridad.escolaridad}}</option>
                            
                        </select>
                        <span class="register__wrapper__vo__form__vp__error uk-text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>

                    <!-- <div class="register__wrapper__vo__form__vp"> -->
                    <ValidationProvider class="register__wrapper__vo__form__vp" name="autorization" rules="autorizar" v-slot="{ errors }">
                      <div class="politicas-container">
                        <input class="uk-checkbox politicas-container__input" type="checkbox" v-model="autorization">
                        <label class="uk-form-label politicas-container__label">
                          He leído y Acepto las <a href="#modal-example" uk-toggle>políticas de privacidad </a> de SEPPTE.
                        </label>
                      </div>
                      <span class="register__wrapper__vo__form__vp__error uk-text-danger">{{ errors[0] }}</span>
                    <!-- </div> -->
                    </ValidationProvider>

                    
                    <button type="submit" class="uk-button uk-button-primary"  v-if="!loading">
                        <span>Registrarte</span>
                    </button>

                    <div class="uk-button uk-button-primary" v-if="loading">
                        <div class="loadingContainer"></div>
                    </div>




                </form>
                <a href="#" uk-totop uk-scroll></a>


            </ValidationObserver>
        
            <!-- This is an anchor toggling the modal -->
            <!-- <a href="#modal-example" uk-toggle>Open</a> -->

            <!-- This is the modal -->
            <div id="modal-example" uk-modal>
                <div class="uk-modal-dialog uk-modal-body">
                    <h2 class="uk-modal-title">Politica de privacidad</h2>

                    <div class="uk-grid">
                      <div>
                        <p class="primary-color">AVISO DE PRIVACIDAD</p>
                        <p class="uk-margin-small-bottom">
                          De conformidad con lo estipulado por la Constitución Política de los Estados Unidos
                          Mexicanos, la Ley General de Protección de Datos Personales en Posesión de Sujetos
                          Obligados y la Ley de Protección de Datos Personales en Posesión de Sujetos Obligados
                          para el estado de Baja California, se emite el presente Aviso de Privacidad para el
                          conocimiento de los participantes en el proyecto de investigación científica denominado
                          &quot;Utilización de Big Data y Ciencia de datos para el estudio de los patrones de la
                          personalidad y trastornos emocionales&quot;.
                        </p>
                      </div>
                      <div class="uk-margin-small-bottom">
                        <p class="uk-margin-small-bottom primary-color">Nombre del responsable:</p>
                        <p class="uk-margin-small-top uk-margin-remove-bottom">Dr. José Manuel Villegas Izaguirre.</p>
                        <p class="uk-margin-small-top uk-margin-remove-top">Villegas_josemanuel@uabc.edu.mx</p>
                      </div>
                      <div  class="uk-margin-small-bottom">
                        <p class="uk-margin-small-bottom primary-color">Domicilio:</p>
                        <p>Edificio de Rectoría en Avenida Álvaro Obregón y Julián Carrillo S/N, Col. Nueva, en la ciudad de Mexicali, Baja California, México. C.P. 21100.</p>
                      </div>
                      <div  class="uk-margin-small-bottom">
                        <p class="uk-margin-small-bottom primary-color">Datos Personales sujetos a tratamiento:</p>
                        <p>Los datos personales que se recabarán y que se encuentran sujetos a tratamientos con las finalidades que se describen en el presente aviso de privacidad.</p>
                        <ul>
                          <li>Nombre.</li>
                          <li>Apellido Paterno</li>
                          <li>Apellido Materno</li>
                          <li>Correo Electrónico.</li>
                          <li>Genero.</li>
                          <li>Estado Civil.</li>
                          <li>Fecha de Nacimiento.</li>
                          <li>Ocupación.</li>
                          <li>Escolaridad.</li>
                        </ul>
                      </div>

                      <div class="uk-margin-small-bottom">
                        <p class="uk-margin-small-bottom primary-color">Finalidades:</p>
                        <p>Las finalidades para la cual se recabarán los datos personales son:</p>
                        <ul>
                          <li>Las finalidades para la cual se recabarán los datos personales son:
Llevar un registro de los participantes por medio del responsable del manejo de los datos, dentro del sistema denominado:  Sistema para el Estudio de los Patrones de la Personalidad y Trastornos Emocionales (SEPPTE)</li>
                          <li>Evidenciar la fidelidad y diversidad de los datos utilizados en el proyecto de investigación.</li>
                          <li>Desarrollo de estudios estadísticos en los que se analizan a la edad, la ocupación y el nivel académico y su interacción con los Patrones de la Personalidad.</li>
                          <li>Desarrollo de nuevas hipótesis y proyectos de investigación subsecuentes.</li>
                        </ul>
                      </div>

                      <div class="uk-margin-small-bottom">
                        <p class="uk-margin-small-bottom primary-color">Seguridad de los datos personales:</p>
                        <p>El responsable del manejo de los datos implementará las medidas de seguridad técnicas, administrativas y físicas, necesarias para proteger sus datos y evitar su alteración o el uso, acceso o tratamiento no autorizado.</p>
                        <p>Únicamente el personal autorizado por el responsable del manejo de los datos, que ha cumplido y observado los correspondientes requisitos de confidencialidad, podrá participar en el tratamiento de sus datos personales. El personal autorizado tiene prohibido el acceso de personas no autorizadas y utilizar sus datos personales para fines distintos a los establecidos en el presente Aviso de Privacidad. La obligación de confidencialidad de las personas que participan en el tratamiento de sus datos personales subsiste aún después de culminado el proyecto de investigación.</p>
                      </div>

                      <div class="uk-margin-small-bottom">
                        <p class="uk-margin-small-bottom primary-color">Comunicación y Transferencia de los datos personales:</p>
                        <p>El responsable del manejo de los datos podrá transferir sus datos personales a otras bases de datos relacionadas con el proyecto de investigación o con proyectos derivados de este.</p>
                        <p>En dichos supuestos, le informamos que el responsable del manejo de los datos optará las medidas necesarias para que las personas que tengan acceso a sus datos personales cumplan con la política de privacidad, así como con los principios de protección de datos personales establecidos en la Ley.</p>
                        <p>Adicionalmente, se informa que, salvo los motivos expuestos en el presente apartado, no se realizaran transferencias de datos personales ante ninguna entidad, sin autorización debida del titular de los datos personales.</p>
                      </div>

                      <div class="uk-margin-small-bottom">
                        <p class="uk-margin-small-bottom primary-color">Ejercicio de Derechos ARCO:</p>
                        <p>El titular de los datos personales podrá́ ejercer sus derechos de Acceso, Rectificación, Cancelación y Oposición (ARCO) solicitando lo conducente y exponiendo motivos enviando un correo a la dirección ivan.herrera91@uabc.edu.mx.</p>
                      </div>

                    </div>
                    <p class="uk-text-right">
                        <!-- <button class="uk-button uk-button-default uk-modal-close" type="button">Cancel</button>
                        <button class="uk-button uk-button-primary" type="button">Save</button> -->
                        <!-- <button class="uk-button uk-button-default uk-modal-close" type="button">Cancel</button> -->
                        <button class="uk-button uk-button-default uk-modal-close" type="button">Cerrar</button>
                    </p>
                </div>

            </div>



        </div>
    </div>
    
</template>

<script>
import { ValidationProvider, extend , ValidationObserver} from 'vee-validate';
import { required, email } from 'vee-validate/dist/rules';
extend('empty', {
  validate: value => {
    return value.length > 5;
  },
  message: 'Ingresa tu nombre'
});

extend('secret', {
  validate: value => value === 'example',
  message: 'This is not the magic word'
});

extend('positive', {
    validate (value) {
        // return value.length > 0
        return {
            required: true,
            valid: value.length > 4,
        };
    },
    computesRequired: true
});

extend('autorizar', {
    validate (value) {
        // return value.length > 0
        return {
            required: true,
            valid: value==true,
        };
    },
    computesRequired: true,
    message: 'Para poder registarse es necesario aceptar las politicas de privacidad'
});

extend('email', email);

extend('email', {
  ...email,
  message: 'Porfavor ingresa un correo valido'
});

// Override the default message.
extend('required', {
  ...required,
  message: 'Este campo es obligatorio'
});

// extend('required', {
//   validate: value => {
//     return true;
//   },
//   message: 'This field must be an odd number'
// });

export default {
    name: 'Register',
    components: {
        ValidationProvider,
        ValidationObserver
    },
    created(){
        let este= this
        if(this.rememberCode != null){
          // console.log('olvidaste tu ususario')
          Swal.fire({
            title: this.rememberCode,
            text: 'Olvidaste tu codigo?',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Iniciar sesion',
            cancelButtonText: 'Registro'
          }).then((result) => {
            if (result.value) {
              este.$router.push("/home", null, function(error) { true });
            }
          })
        }
        this.$store.dispatch('getRegisterData')
        .then(response => {
            console.log(response)
            this.genders= response.genders
            this.ms= response.marital
            this.jobs= response.jobs
            this.escolaridades = response.escolaridades
        })
        .catch(error => {
            this.launchNotification()
        })
    },
    data(){
        return {
            welcome: 'Bienvenido',
            name: '',
            apaterno: '',
            amaterno: '',
            gender: '',
            marital: '',
            birthday: '',
            job: '',
            escolaridad: '',
            email: '',
            genders: null,
            ms: null,
            jobs: null,
            errors: [],
            errorServer: null,
            loading: false, 
            code: null,
            escolaridades: null,
            autorization: false
        }
    },
    computed: {
        // loading(){
        //     return this.$store.getters.getLoading
        // }
        rememberCode(){
          return this.$store.getters.getRememberCode
        }
    },
    methods:{

        openModalPoliticas(){
          console.log('Ioe')
        },

        launchNotification(){
            UIkit.notification({
                message: 'Error, porfavor intente mas tarde',
                status: 'danger',
                pos: 'top-center',
                timeout: 5000
            });
        },
        onSubmit(){

            if(!this.autorization){
              console.log('Para poder registrarse se deben de aceptar los terminos y condiciones')
              // return false
            }

            let este= this;
            este.loading=true
            this.$store.dispatch('storeUser', {
                name: this.name,
                apaterno: this.apaterno,
                amaterno: this.amaterno,
                gender: this.gender,
                marital: this.marital,
                birthday: this.birthday,
                job: this.job,
                email: this.email,
                escolaridad: this.escolaridad
            })
            .then(response => {
                este.code= response
                este.loading=false
                this.$store.commit('setRememberCode',response)
                Swal.fire({
                    icon: 'success',
                    title: response,
                    text: 'Registro exitoso, porfavor copie su codigo de usuario para proceder a iniciar sesion',
                })
                .then((result) => {
                    this.$router.push({name: 'login'})
                })
                
                console.log(response)
            })
            .catch(error => {
                // console.log()
                este.loading=false
                this.errors= []
                if(error.status == 422){
                    let e= error.data;
                    let este= this
                    console.log(e)
                    // this.errors= e.errors
                    Object.keys(e.errors).forEach(function(key) {
                        let arr= e.errors[key];
                        // console.log(arr)
                        este.errors.push(arr[0])
                    });
                    scroll(0,0)
                }else{
                    this.errors.push('Error, porfavor intentelo mas tarde')
                    this.launchNotification()
                }
            })
        },
        
    }
    
}
</script>>

<style lang="scss">
@import '../../scss/variables.scss';

.register{
    width: 100%;
    background-color: $background;
    padding: 1em;
    &__wrapper{
        max-width: 560px;
        // background: tomato;
        background-color: white;
        box-shadow: 0 6px 6px rgba(0,0,0,0.2);
        margin: 0 auto;
        padding: 3em;
        // border-radius: 45px;
        &__vo{
            width: 100%;
            &__form{
                width: 100%;
                display: flex;
                // flex-wrap: wrap;
                flex-direction: column;
                justify-content: flex-start;
                &__vp{
                    width: 100%;
                    display: flex;
                    flex-wrap: wrap;
                    margin-bottom: 1em;
                    &__label{
                        width: 100%;
                        margin-bottom: 0.5em;
                    }

                    &__input{
                        // width: 100%;

                    }

                    &__error{
                        width: 100%;

                    }
                }
            }
        }
    }
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.loadingContainer{
    padding:1em
}
.loader {
  color: #ffffff;
  font-size: 20px;
  text-indent: -9999em;
  overflow: hidden;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  margin: 10px auto;
//   background: red;
  position: relative;
  transform: translateZ(0);
  animation: load6 1.7s infinite ease, round 1.7s infinite ease;
}
@-webkit-keyframes load6 {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  5%,
  95% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  10%,
  59% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
  }
  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em;
  }
  38% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em;
  }
  100% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
}
@keyframes load6 {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  5%,
  95% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  10%,
  59% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
  }
  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em;
  }
  38% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em;
  }
  100% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
}
@-webkit-keyframes round {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes round {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}


.politicas-container{
  display: flex;
  // justify-content: center;
  align-items: center;
  &__label{
    margin-left: 12px;
    &__politicas{
      color: rgb(30, 135, 240);
      cursor: pointer;
    }
  }
  &__checkbox{
    margin: 0;
  }
}

.primary-color{
  color: rgb(30, 135, 240);
}

// .subtitulo{
//   color:  rgb(30, 135, 240);
//   margin-bottom: 0.5em;
